import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {AdaptCarouselComponent, AdaptCarouselSlideComponent} from './carousel.component';


@NgModule({
  imports: [CommonModule],
  exports: [AdaptCarouselComponent, AdaptCarouselSlideComponent],
  declarations: [AdaptCarouselComponent, AdaptCarouselSlideComponent],
})

export class AdaptCarouselModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptCarouselModule> {
    return {
      ngModule: AdaptCarouselModule
    };
  }
}

export * from './carousel.component';
export * from './carousel.model';
